import React from "react"
import Layout from "../components/layout"

const PomocPriPromociji = () => (
  <Layout>
    <div className="promocija-content">
      <p className="promotion-text">
        Ja, nekateri živijo svoje sanje, spet drugi so v nizkem štartu in
        tretji… odlašajo in iščejo izgovore: »še ni zrelo«, ipd…{" "}
        <img
          className="emoji-img"
          src="../icons/wink.png"
          width="30px"
          alt=""
        />
        <br />
        <br />
        Imaš idejo, znanje, veselje in produkt, a zadeva nikakor ne steče tako,
        kot si želiš.
        <br />
        <br />
        Včasih je potrebno, da te nekdo "spromovira", govori o tvojem čudovitem
        izdelku, znanju ali storitvi. Moja strast in želja je, da si pomagamo,
        da se povežemo.
        <br />
        <br />
        Zato, če potrebuješ malo pomoči pri »vzletu«, sem tu{" "}
        <img
          className="emoji-img"
          src="../icons/wink.png"
          width="30px"
          alt=""
        />
        . Vsi vemo, da ko te nekdo priporoči ali govori o tvoji strasti, ki jo
        želiš deliti z drugimi, gre veliko hitreje.
        <br />
        <br />
        Spoznajva se, poglejva kaj ponujaš oziroma kaj iščeš in kako ti lahko
        pomagam. Mogoče pa potrebuješ samo potrditev, da si na pravi poti.{" "}
        <img
          className="emoji-img"
          src="../icons/wink.png"
          width="30px"
          alt=""
        />
        <br />
        <br />
        Poznate tisti rek: "Dobro blago se samo hvali". Jaz pa dodajam, še bolje
        in hitreje pa je, če ti nekdo pri tem pomaga.{" "}
        <img
          className="emoji-img"
          src="../icons/wink.png"
          width="30px"
          alt=""
        />
        <br />
        <br />
      </p>

      <div className="gallery-content">
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/1-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-1"
            src="../assets/1-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/4-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-4"
            src="../assets/4-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/7-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-7"
            src="../assets/7-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/5-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-5"
            src="../assets/5-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/6-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-6"
            src="../assets/6-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/2-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-2"
            src="../assets/2-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
        <picture className="kom-pict-img">
          <source
            srcSet="../assets/webp/3-p.webp"
            alt="Logo"
            type="image/webp"
          />
          <img
            className="gallery-img img-3"
            src="../assets/3-p.png"
            alt="slika"
            type="image/png"
          />
        </picture>
      </div>
      {/* 
      <div className="gallery-content">
        <img className="gallery-img img-1" src="../assets/1-p.jpg" alt="" />

        <img className="gallery-img img-4" src="../assets/4-p.jpg" alt="" />
        <img className="gallery-img img-7" src="../assets/7-p.jpg" alt="" />

        <img className="gallery-img img-5" src="../assets/5-p.jpg" alt="" />

        <img className="gallery-img img-6" src="../assets/6-p.jpg" alt="" />
        <img className="gallery-img img-2" src="../assets/2-p.jpg" alt="" />
        <img className="gallery-img img-3" src="../assets/3-p.jpg" alt="" />
      </div> */}
    </div>
  </Layout>
)

export default PomocPriPromociji
